.loginCustomHook {
  width: 100%;

  h1 {
    margin-bottom: 2rem;
  }

  &__register {
    display: flex;
    align-items: center;
    flex-direction: column;
  }
}

.form {
  position: relative;

  display: flex;
  flex-direction: column;
  justify-content: center;
}

.error {
  font-size: .75rem;

  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  padding: 0 0 .25rem .25rem;
  color: #d3cb55;
}

.button {
  //margin-right: 0.25rem;
  font-size: .75rem;
  border-color: #372411;
}

.button.submit {
  font-size: 1rem;
  font-weight: bold;
  margin: 0 0 .5rem 0;

  background: #04809d;
}

.button.submit:disabled {
  opacity: .8;
}

.button.submit:hover {
  scale: 103%;
}

.input-wrapper {
  position: relative;
  width: 100%;
}

.input {
  box-sizing: border-box;
  width: 100%;
  margin-bottom: 0;
  outline: 0;
}

.input::placeholder {
  color: #a55200;
}

.icon {
  position: absolute;
  top: 1.1rem;
  right: .5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  transform: translateY(-50%);
  color: #a55200;
}

.input-container {
  position: relative;
  height: 3.75rem;
}

.errorBox {
  display: flex;
}
